import { Component, Input , ViewChild } from '@angular/core';
import { Nav, Platform, IonicPage, NavController, NavParams } from 'ionic-angular';
import { DashboardPage } from '../../pages/dashboard/dashboard';
/**
 * Generated class for the LoginPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-login',
  templateUrl: 'login.html',
})
export class LoginPage {
  @ViewChild(Nav) 
  
  
  @Input() data: any;
    @Input() events: any;
    page;
    public username: string;
    public password: string;

    public isUsernameValid: boolean;
    public isPasswordValid: boolean;
    rootPage: any = LoginPage;
    public error: false;
    
     constructor(public navCtrl: NavController, public navParams: NavParams,public platform: Platform) {
        this.isUsernameValid= true;
        this.isPasswordValid = true;
  }  

  ionViewDidLoad() {
    console.log('ionViewDidLoad LoginPage');
  }
  
  loginme(username,password)
  {
      if (!username ||username.length == 0 ||username == undefined) {
            this.isUsernameValid = false;
        }
       else
       {
          this.isUsernameValid = true;
       }
        if (!password || password.length == 0 || password == undefined) {
            this.isPasswordValid = false;
        }
         else
       {
          this.isPasswordValid = true;
       }
        
     if(this.isUsernameValid && this.isPasswordValid)  
     {
        if(username == 'test' && password == 'test')
        {
            localStorage.login = 1;
            this.navCtrl.setRoot(DashboardPage); 
        }
        else
        {
           alert('Incorrect username and password, Please try again.')
        }
     } 
      
  }

}



